import { request, success, failure } from "../../actions/users/load-all-users";

export const initialState = {
  data: [],
  isLoading: false
};

export const allUsers = (state = initialState, action) => {
  switch (action.type) {
    case request: {
      return {
        ...state,
        isLoading: true
      };
    }
    case success: {
      state.data = action.payload.data;
      // console.log(data);
      // state.isLoading = false;
      return {
        ...state,
        isLoading: false
      };
    }

    case failure:
      return {
        ...state,
        isLoading: false
      };
    default:
      break;
  }
  return state;
};
